import { isTrustedToken } from '@/utils'
import { ErrorTips, FeeTierSelect, PoolsDropSelect } from '@cetus/design'
import { FeeTier } from '@cetus/design/src/components/common/FeeTierSelect'
import { Token } from '@cetus/types'
import { Icon, SingleCoinImage } from '@cetus/ui-kit'
import { textEllipses } from '@cetus/utils'
import {
  Box,
  Button,
  CenterProps,
  Heading,
  HStack,
  MenuButton,
  Popover,
  PopoverBody,
  PopoverContent,
  PopoverTrigger,
  Skeleton,
  SkeletonCircle,
  Stack,
  StackProps,
  Text,
  TextProps,
  useDisclosure,
  VStack
} from '@chakra-ui/react'
import { maxBy } from 'lodash-es'
import { useEffect, useMemo, useRef } from 'react'
import { useNavigate } from 'react-router-dom'
export interface SelectPoolProps {
  fromSource: 'createPool' | 'addLiquidity'
  baseToken?: Token
  quoteToken?: Token
  feeTier?: FeeTier
  feeTierList: FeeTier[]
  onBaseTokenChange: (token?: Token) => void
  onQuoteTokenChange: (token?: Token) => void
  onFeeTierChange: (fee: FeeTier) => void
  title: string
  quoteWhiteTokenList?: Token[]
  description: string
  onContinue: (params: { baseToken?: Token; quoteToken?: Token; feeTier?: Partial<FeeTier> }) => void
  wrapStyle?: StackProps
}

function SelectPool({
  baseToken,
  quoteToken,
  onBaseTokenChange,
  onQuoteTokenChange,
  feeTier,
  feeTierList,
  onFeeTierChange,
  title,
  description,
  onContinue,
  quoteWhiteTokenList,
  fromSource,
  wrapStyle = {}
}: SelectPoolProps) {
  const navigate = useNavigate()

  useEffect(() => {
    if (fromSource === 'addLiquidity' && feeTierList?.length > 0) {
      const _pool = maxBy(feeTierList, item => Number(item?.tvl || '0'))

      if (_pool) {
        onFeeTierChange(_pool)
      }
    }
  }, [fromSource, feeTierList])

  const handleBaseTokenChange = (token: Token) => {
    if (token?.coin_type === quoteToken?.coin_type) {
      onQuoteTokenChange(undefined)
    }
    onBaseTokenChange(token)
  }
  const handleQuoteTokenChange = (token: Token) => {
    if (token?.coin_type === baseToken?.coin_type) {
      onBaseTokenChange(undefined)
    }
    onQuoteTokenChange(token)
  }

  const showTokenWarn = useMemo(() => {
    return baseToken && quoteToken && !isTrustedToken(baseToken, quoteWhiteTokenList) && !isTrustedToken(quoteToken, quoteWhiteTokenList)
  }, [baseToken?.coin_type, quoteToken?.coin_type])

  console.log('🚀🚀🚀 ~ file: SelectPool.tsx:104 ~ quoteWhiteTokenList:', baseToken, quoteToken, showTokenWarn, quoteWhiteTokenList)

  const feeBtnRef = useRef<HTMLButtonElement>(null)

  return (
    <VStack w="100%" gap={{ base: '16px', lg: '28px' }} bg="bg_fifth" borderRadius="12px" p={{ base: '16px 8px', lg: '24px' }} {...wrapStyle}>
      <VStack gap="8px" w="100%" align="flex-start">
        <Heading fontSize="16px" fontWeight="500">
          {title}
        </Heading>
        <Text fontSize="12px" fontWeight="500">
          {description}
        </Text>
      </VStack>
      <VStack w="100%" gap={{ base: '16px', lg: '32px' }}>
        <Stack flexDir={{ base: 'column', lg: 'row' }} w="100%" gap="12px" justify="space-between">
          <SelectToken title="Base token" value={baseToken} isWhiteSelect={false} whiteTokenList={undefined} onChange={handleBaseTokenChange} />
          <SelectToken
            title="Quote token"
            value={quoteToken}
            isWhiteSelect={true}
            whiteTokenList={quoteWhiteTokenList}
            onChange={handleQuoteTokenChange}
          />
        </Stack>
        <VStack w="100%" align="flex-start" gap="16px">
          <VStack w="100%" align="flex-start" gap="8px">
            <Text fontSize="16px" fontWeight="500" color="text_caption">
              Fee tier
            </Text>
            <Text
              fontSize="12px"
              fontWeight="500"
            >{`The ${(typeof feeTier === 'object' && feeTier?.feeDisplay) || '%'} you will earn in fees.`}</Text>
          </VStack>
          <FeeTierSelect
            value={feeTier}
            onChange={fee => onFeeTierChange(fee)}
            feeTierList={feeTierList}
            placement="bottom"
            wrapStyle={{ w: feeBtnRef?.current?.clientWidth || '463px' }}
          >
            {({ isOpen, onClick }) => (
              <MenuButton
                w="100%"
                borderRadius="12px"
                h="48px"
                border="1px solid"
                borderColor="border"
                cursor="pointer"
                onClick={onClick}
                bg="bg_secondary"
                ref={feeBtnRef}
              >
                <HStack w="100%" justifyContent="space-between" p="9px 16px">
                  {feeTier ? (
                    <Text color="text_caption" fontSize="16px" fontWeight="500">
                      {`${typeof feeTier === 'object' && feeTier?.feeDisplay}`}
                    </Text>
                  ) : (
                    <Text color="text_caption" fontSize="14px" fontWeight="500">
                      Select fee tier
                    </Text>
                  )}
                  <HStack>
                    <Text fontSize="12px">{typeof feeTier === 'object' && feeTier?.title}</Text>
                    <Icon
                      xlinkHref="#icon-icon_arrow"
                      fontSize="12px"
                      transition="transform 0.5s"
                      transform={isOpen ? 'rotate(180deg)' : 'rotate(0deg)'}
                    />
                  </HStack>
                </HStack>
              </MenuButton>
            )}
          </FeeTierSelect>

          {showTokenWarn && (
            <ErrorTips
              isShowIcon={false}
              p="6px 16px"
              w="100%"
              borderRadius="8px"
              tipsLineHeight="16px"
              tipsFontSize="12px"
              tips="To create a pool, one of the assets needs to be selected as the default token."
            />
          )}
        </VStack>
        <Button
          w="100%"
          borderRadius="12px"
          h="48px"
          disabled={!baseToken || !quoteToken || !feeTier || feeTier?.disabled || showTokenWarn}
          onClick={() => onContinue({ baseToken, quoteToken, feeTier })}
        >
          Continue
        </Button>
      </VStack>
    </VStack>
  )
}

export default SelectPool

interface SelectTokenProps {
  title?: string
  value?: Token
  isWhiteSelect: boolean
  whiteTokenList?: Token[]
  onChange: (token: Token) => void | Promise<void>
  disabled?: boolean
  tokenStyle?: CenterProps
  tokenSize?: string
  symbolStyle?: TextProps
  wrapStyle?: StackProps
  loading?: boolean
}
export const SelectToken = ({
  title,
  value,
  onChange,
  whiteTokenList,
  isWhiteSelect,
  disabled = false,
  wrapStyle = {},
  symbolStyle = {},
  tokenSize = '28px',
  tokenStyle = {},
  loading = false
}: SelectTokenProps) => {
  const { isOpen, onOpen, onClose } = useDisclosure()
  const firstItemRef = useRef(null)

  return (
    <>
      <Popover
        modifiers={[
          {
            name: 'flip',
            enabled: false // Disable automatic flip
          },
          {
            name: 'shift',
            enabled: false // Disable automatic shift
          }
        ]}
        initialFocusRef={firstItemRef}
        isLazy
        isOpen={isOpen}
        trigger="click"
        onClose={onClose}
        onOpen={onOpen}
        placement="bottom-start"
      >
        <PopoverTrigger>
          <Box w="100%" as="button" ref={firstItemRef} disabled={disabled}>
            {loading ? (
              <HStack>
                <SkeletonCircle w={tokenSize} h={tokenSize} />
                <Skeleton h={symbolStyle.fontSize || '16px'} />
              </HStack>
            ) : (
              <VStack flex="1" gap="8px" align="flex-start">
                {title && (
                  <Text fontSize="12px" fontWeight="500">
                    {title}
                  </Text>
                )}
                <HStack
                  h="48px"
                  w="100%"
                  justify="space-between"
                  p="9px 16px"
                  cursor="pointer"
                  onClick={onOpen}
                  borderRadius="12px"
                  border="1px solid"
                  borderColor="border"
                  bg="bg_secondary"
                  _hover={{
                    svg: {
                      fill: 'text_caption'
                    }
                  }}
                  {...wrapStyle}
                >
                  {value ? (
                    <HStack>
                      <SingleCoinImage imageUrl={value?.logo_url} w={tokenSize} h={tokenSize} coinType={value?.coin_type} {...tokenStyle} />
                      <Text color="text_caption" fontSize="16px" fontWeight="600" whiteSpace="nowrap" {...symbolStyle}>
                        {textEllipses(value?.symbol)}
                      </Text>
                    </HStack>
                  ) : (
                    <Text color="text_caption" fontSize="14px" fontWeight="500" whiteSpace="nowrap">
                      Select token
                    </Text>
                  )}
                  <Icon
                    xlinkHref="#icon-icon_arrow"
                    fontSize="12px"
                    transition="transform 0.5s"
                    transform={isOpen ? 'rotate(180deg)' : 'rotate(0deg)'}
                  />
                </HStack>
              </VStack>
            )}
          </Box>
        </PopoverTrigger>
        <PopoverContent w={firstItemRef?.current?.clientWidth || '224px'} minW="224px">
          <PopoverBody p="0">
            <PoolsDropSelect
              selectTokenList={value ? [value] : []}
              onClickToken={(token: Token) => {
                onChange(token)
                onClose()
              }}
              whiteTokenList={whiteTokenList}
              hideCheckbox
            />
          </PopoverBody>
        </PopoverContent>
      </Popover>

      {/* <TokenSelectModal
        tokenInfo={value}
        lastSelectedToken={value?.coin_type || ''}
        isOpen={isOpen}
        isDcaSelect={isWhiteSelect}
        isShowCollectListBox={title !== 'Quote token'}
        isShowTokenListTab={title !== 'Quote token'}
        isShowLabelTab={title !== 'Quote token'}
        whiteTokenList={whiteTokenList}
        onSelectToken={(tokenInfo: Token) => {
          onChange(tokenInfo)
        }}
        onClose={onClose}
      /> */}
    </>
  )
}
